<template>
  <div>
    <!-- Hamburger Menu -->
    <div class="hamburger-menu" @click="toggleSidebar">
      <div class="hamburger-icon"></div>
    </div>
    <!-- Sidebar Content -->
    <div class="sidebar" :class="{ active: isSidebarActive }">
      <h2></h2>
      <router-link to="/backend/dashboard"></router-link>
      <router-link to="/backend/user-management"></router-link>
      <router-link to="/backend/role-management"></router-link>
      <router-link to="/backend/history"></router-link>
      <router-link to="/backend/preferences"></router-link>
      <router-link to="/backend/profile"></router-link>
      <button class="logout-btn" @click="logout"></button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    isSidebarActive: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    toggleSidebar() {
      this.$emit('toggle-sidebar'); // Emit an event to the parent component to handle the toggle
    },
    logout() {
      this.$router.push('/backend/login');
    }
  }
};
</script>

<style scoped src="@/assets/css/backend/SideBar.css"></style>
