<template>
  <div>
    <button class="hamburger-menu" @click="toggleMenu">
      <div></div>
      <div></div>
      <div></div>
    </button>

    <div class="content" id="content">
      <div class="container">
        <div class="preferences">
          <div class="header"></div>
          <div class="form-group">
            <label for="font-size"></label>
            <select id="font-size" v-model="fontSize">
              <option value="large"></option>
              <option value="medium"></option>
              <option value="small"></option>
            </select>
          </div>
          <div class="form-group">
            <label for="notification"></label>
            <select id="notification" v-model="notification">
              <option value="enable"></option>
              <option value="disable"></option>
            </select>
          </div>
          <div class="form-group">
            <label for="auto-login"></label>
            <select id="auto-login" v-model="autoLogin">
              <option value="enable"></option>
              <option value="disable"></option>
            </select>
          </div>
          <div class="form-group">
            <label for="authentication"></label>
            <select id="authentication" v-model="authentication">
              <option value="enable"></option>
              <option value="disable"></option>
            </select>
          </div>
          <div class="buttons">
            <button class="cancel-button" @click="cancelChanges"></button>
            <button class="submit-button" @click="submitChanges"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PreferenceSetting',
  data() {
    return {
      fontSize: 'medium',
      notification: 'disable',
      autoLogin: 'disable',
      authentication: 'disable',
    };
  },
  methods: {
    toggleMenu() {
      const dropdownContent = document.querySelector('.dropdown-content');
      dropdownContent.style.display = dropdownContent.style.display === 'block' ? 'none' : 'block';
    },
    cancelChanges() {
      this.fontSize = 'medium';
      this.notification = 'disable';
      this.autoLogin = 'disable';
      this.authentication = 'disable';
    },
    submitChanges() {
      const fontSize = this.fontSize === 'large' ? '18px' : this.fontSize === 'small' ? '12px' : '16px';
      document.body.style.fontSize = fontSize;
      alert('');
    }
  },
};
</script>

<style src="@/assets/css/frontend/PreferenceSetting.css"></style>
