<template>
  <div class="container">
    <h2></h2>
    <div class="form-group">
      <label for="permission-name"></label>
      <input type="text" v-model="newPermission.permission_name" id="permission-name" required />
    </div>
    <div class="form-group">
      <h3></h3>
      <div class="checkbox-container">
        <label><input type="checkbox" v-model="newPermission.can_add" /> </label>
        <label><input type="checkbox" v-model="newPermission.can_query" /> </label>
        <label><input type="checkbox" v-model="newPermission.can_view" /> </label>
        <label><input type="checkbox" v-model="newPermission.can_edit" /> </label>
        <label><input type="checkbox" v-model="newPermission.can_delete" /> </label>
        <label><input type="checkbox" v-model="newPermission.can_print" /> </label>
        <label><input type="checkbox" v-model="newPermission.can_export" /> </label>
        <label><input type="checkbox" v-model="newPermission.can_maintain" /> </label>
      </div>
    </div>
    <button @click="savePermission"></button>
    <button @click="cancelPermission"></button>
  </div>
</template>

<script>
import axios from '@/axios'; 

export default {
  data() {
    return {
      newPermission: {
        permission_name: '',
        can_add: false,
        can_query: false,
        can_view: false,
        can_edit: false,
        can_delete: false,
        can_print: false,
        can_export: false,
        can_maintain: false,
      },
    };
  },
  methods: {
    async savePermission() {
      try {
        const response = await axios.post('/api/backend/role_permissions/', {
          ...this.newPermission,
          role: this.$route.params.roleId,
        });
        if (response.status >= 200 && response.status < 300) {
          alert('');
          this.$emit('permission-saved');  // 
          this.$router.back();  // 
        } else {
          alert('');
        }
      } catch (error) {
        console.error('Error saving permission:', error.response ? error.response.data : error.message);
        alert(`: ${error.response ? error.response.data.detail : error.message}`);
      }
    },
    cancelPermission() {
      this.$emit('permission-cancelled');  // 
      this.$router.back();  // 
    },
  }
};
</script>

<style scoped src="@/assets/css/backend/RolePermissions.css"></style>
