<template>
  <div>
    <div :class="['sidebar', { open: isSidebarOpen }]">
      <div class="sidebar-header">
        <font-awesome-icon icon="user" class="icon" />
        <span v-if="isSidebarOpen"></span> <!--  -->
        <span class="toggle-btn" @click="toggleSidebar"></span>
      </div>
      <router-link to="/frontend/home" data-tooltip="">
        <font-awesome-icon icon="tachometer-alt" class="icon" />
        <span class="text"></span>
      </router-link>
      <router-link to="/frontend/profile" data-tooltip="">
        <font-awesome-icon icon="user" class="icon" />
        <span class="text"></span>
      </router-link>
      <router-link to="/frontend/accountsettings" data-tooltip="">
        <font-awesome-icon icon="users" class="icon" />
        <span class="text"></span>
      </router-link>
      <router-link to="/frontend/history" data-tooltip="">
        <font-awesome-icon icon="history" class="icon" />
        <span class="text"></span>
      </router-link>
      <a href="#" @click.prevent="confirmLogout" data-tooltip="">
        <font-awesome-icon icon="sign-out-alt" class="icon" />
        <span class="text"></span>
      </a>
    </div>
    <div :class="['content', { shift: isSidebarOpen }]">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "SidebarPage",
  data() {
    return {
      isSidebarOpen: false
    };
  },
  methods: {
    toggleSidebar() {
      this.isSidebarOpen = !this.isSidebarOpen;
    },
    confirmLogout() {
      const confirmed = confirm("");
      if (confirmed) {
        this.logout();
      }
    },
    logout() {
      alert("");
      this.$router.push('/frontend/login');
    }
  }
};
</script>

<style scoped src="@/assets/css/frontend/SidebarPage.css"></style>
