<template>
  <div class="home-page">
    <!--  -->
    <TopNavbar @export="exportChart" />
    
    <!--  -->
    <div class="branch-selection">
      <label for="branch-select">:</label>
      <select id="branch-select" v-model="selectedBranch" @change="onBranchChange">
        <option v-for="branch in branches" :key="branch.branch_id" :value="branch.branch_id">
          {{ branch.branch_name }}
        </option>
      </select>
    </div>
    
    <div class="chart-controls">
      <!--  -->
      <button @click="setCurrentChart('RevenueChart')"></button>
      <button @click="setCurrentChart('SalesChart')"></button>
      <button @click="setCurrentChart('InventoryChart')"></button>
    </div>
    
    <div class="chart-container">
      <!--  currentChart  -->
      <component :is="currentChart" :branch-id="selectedBranch" />
    </div>
  </div>
</template>

<script>
// 
import TopNavbar from '@/components/frontend/TopNavbar.vue';
import SalesChart from '@/Charts/SalesChart.vue';
import RevenueChart from '@/Charts/RevenueChart.vue';
import InventoryChart from '@/Charts/InventoryChart.vue';
import axios from 'axios';

export default {
  name: 'HomePage',
  components: {
    TopNavbar,
    SalesChart,
    RevenueChart,
    InventoryChart,
  },
  data() {
    return {
      currentChart: 'SalesChart', // 
      branches: [], // 
      selectedBranch: null, // 
    };
  },
  methods: {
    async fetchBranches() {
      try {
        const response = await axios.get('http://127.0.0.1:8000/api/branches/'); //  URL  API
        this.branches = response.data;
        this.selectedBranch = this.branches.length > 0 ? this.branches[0].branch_id : null;
      } catch (error) {
        console.error('Error fetching branches:', error);
      }
    },
    setCurrentChart(chart) {
      this.currentChart = chart;
    },
    onBranchChange() {
      // 
      console.log('Selected branch:', this.selectedBranch);
    },
  },
  async mounted() {
    await this.fetchBranches(); // 
  },
};
</script>

<style src="@/assets/css/frontend/HomePage.css"></style>
