<template>
  <div>
    <SidebarPage /> <!--  SidebarPage  -->
    <div class="history-page">
      <div class="container">
        <!--  -->
        <div v-if="!isLoggedIn" class="no-history centered-content">
          <p></p>
        </div>
        <!--  -->
        <div v-else>
          <div class="search-container centered-content">
            <input type="text" id="searchInput" placeholder="..." v-model="searchQuery">
            <button @click="filterTimeline"></button>
          </div>
          <ul class="timeline centered-content" id="timeline">
            <li class="timeline-item" v-for="item in filteredItems" :key="item.id" @click="showDetail(item.id)">
              <div class="timeline-panel">
                <div class="timeline-heading">
                  <h4>{{ item.date }}</h4>
                </div>
                <div class="timeline-body">
                  <p>{{ item.action }}</p>
                  <div class="timeline-user">: {{ item.user }}</div>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <ul class="timeline" id="timeline">
        <li class="timeline-item" v-for="item in filteredItems" :key="item.id" @click="showDetail(item.id)">
          <div class="timeline-panel">
            <div class="timeline-heading">
              <h4>{{ item.date }}</h4>
            </div>
            <div class="timeline-body">
              <p>{{ item.action }}</p>
              <div class="timeline-user">: {{ item.user }}</div>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import SidebarPage from '@/components/frontend/SidebarPage.vue'; //  SidebarPage 
import axios from '@/axios';

export default {
  name: 'HistoryPage',
  components: {
    SidebarPage //  SidebarPage 
  },
  data() {
    return {
      searchQuery: '',
      items: [], //  API 
      isLoggedIn: false, // 
    }
  },
  computed: {
    filteredItems() {
      return this.items.filter(item => {
        const query = this.searchQuery.trim().toUpperCase();
        return item.action.toUpperCase().includes(query) || item.date.includes(query);
      });
    }
  },
  methods: {
    filterTimeline() {
      // 
    },
    showDetail(id) {
      this.$router.push(`/detail/${id}`);
    },
    async checkLoginStatus() {
      try {
        const response = await axios.get('/api/frontend/check_login_status'); //  API 
        this.isLoggedIn = response.data.loggedIn;
        if (this.isLoggedIn) {
          this.fetchHistory(); // 
        } else {
          this.$router.pugh('/login');
        }
      } catch (error) {
        console.error(':', error);
      }
    },
    async fetchHistory() {
      try {
        const response = await axios.get('/api/frontend/history/'); //  API 
        this.items = response.data; //  items
      } catch (error) {
        console.error(':', error);
      }
    }
  },
  mounted() {
    this.checkLoginStatus(); // 
  }
}
</script>

<style scoped>
.history-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /*  */
}

.container {
  width: 100%;
  max-width: 800px; /*  */
  margin: 0 auto;
  padding: 20px;
  text-align: center; /*  */
}

.centered-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.no-history {
  font-size: 18px;
  color: #555;
}

.search-container {
  margin-bottom: 20px;
}

.timeline {
  list-style: none;
  padding: 0;
}

.timeline-item {
  margin-bottom: 20px;
}

.timeline-panel {
  background: #f8f9fa;
  border-radius: 5px;
  padding: 15px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: left; /*  */
}
</style>
