<template>
  <div class="login-container">
    <div class="login-form">
      <h2></h2>
      <form @submit.prevent="handleLogin">
        <div class="input-group">
          <label for="email"></label>
          <input type="email" id="email" v-model="email" required />
        </div>
        <div class="input-group">
          <label for="password"></label>
          <div class="input-container">
            <input type="password" id="password" v-model="password" required />
            <i class="fas fa-eye toggle-password" @click="togglePasswordVisibility('password')"></i>
          </div>
        </div>
        <div class="additional-options">
          <label>
            <input type="checkbox" id="rememberMe" v-model="rememberMe" />
            
          </label>
          <router-link to="/frontend/forgot_password">?</router-link>
        </div>
        <input type="submit" value="" class="submit-button" />
        <div id="login-feedback" class="feedback">{{ loginFeedback }}</div>
      </form>
      <p> <router-link to="/frontend/register"></router-link></p>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      email: '',
      password: '',
      rememberMe: false,
      loginFeedback: ''
    };
  },
  methods: {
    async handleLogin() {
      try {
        //  POST  Django  /api/token/ 
        const response = await axios.post('http://127.0.0.1:8000/api/token/', {
          email: this.email,
          password: this.password
        });
        
        if (response.status === 200) {
          //  token  localStorage  sessionStorage
          localStorage.setItem('token', response.data.access);
          localStorage.setItem('position', response.data.position);  // 
          localStorage.setItem('branch_id', response.data.branch_id); //  ID

          if (this.rememberMe) {
            localStorage.setItem('refreshToken', response.data.refresh);
          } else {
            sessionStorage.setItem('refreshToken', response.data.refresh);
          }

          this.loginFeedback = '';
          this.redirectUserBasedOnPosition(); // 
        }
      } catch (error) {
        if (error.response && error.response.data) {
          this.loginFeedback = error.response.data.detail || '';
        } else {
          this.loginFeedback = '';
        }
      }
    },
    togglePasswordVisibility(inputId) {
      const passwordInput = document.getElementById(inputId);
      if (passwordInput.type === 'password') {
        passwordInput.type = 'text';
      } else {
        passwordInput.type = 'password';
      }
    },
    redirectUserBasedOnPosition() {
      const position = localStorage.getItem('position');

      if (position === '') {
        this.$router.push('/frontend/manager_dashboard'); // 
      } else if (position === '') {
        this.$router.push('/frontend/employee_dashboard'); // 
      } else {
        this.$router.push('/frontend/home'); // 
      }
    }
  }
};
</script>


<style scoped src="@/assets/css/frontend/LoginPage.css"></style>
