<template>
  <div class="app-container">
    <div class="app-content" :class="{ shifted: isSidebarActive }">
      <h1></h1>
      <form @submit.prevent="updatePreferences">
        <div class="form-group">
          <label for="font-size">:</label>
          <select v-model="preferences.font_size" id="font-size">
            <option value="small"></option>
            <option value="medium"></option>
            <option value="large"></option>
          </select>
        </div>
        <div class="form-group">
          <label for="notifications">:</label>
          <input type="checkbox" v-model="preferences.notifications" id="notifications"> 
        </div>
        <div class="form-group">
          <label for="auto-login">:</label>
          <input type="checkbox" v-model="preferences.auto_login" id="auto-login"> 
        </div>
        <div class="form-group">
          <label for="verification">:</label>
          <input type="checkbox" v-model="preferences.verification" id="verification"> 
        </div>
        <div class="btn-container">
          <button type="submit" class="btn"></button>
          <button type="button" class="btn" @click="showAddModal"></button>
          <button type="button" class="btn btn-delete" @click="deletePreference"></button>
          <button type="button" class="btn btn-cancel" @click="resetPreferences"></button>
        </div>
      </form>
  
      <div v-if="successMessage" class="success-message"></div>
  
      <!--  -->
      <div v-if="showModal" class="modal">
        <div class="modal-content">
          <span class="close" @click="closeAddModal">&times;</span>
          <h2></h2>
          <form @submit.prevent="addPreference">
            <div class="form-group">
              <label for="add-font-size">:</label>
              <select v-model="newPreference.font_size" id="add-font-size">
                <option value="small"></option>
                <option value="medium"></option>
                <option value="large"></option>
              </select>
            </div>
            <div class="form-group">
              <label for="add-notifications">:</label>
              <input type="checkbox" v-model="newPreference.notifications" id="add-notifications"> 
            </div>
            <div class="form-group">
              <label for="add-auto-login">:</label>
              <input type="checkbox" v-model="newPreference.auto_login" id="add-auto-login"> 
            </div>
            <div class="form-group">
              <label for="add-verification">:</label>
              <input type="checkbox" v-model="newPreference.verification" id="add-verification"> 
            </div>
            <div class="btn-container">
              <button type="submit" class="btn"></button>
              <button type="button" class="btn btn-cancel" @click="closeAddModal"></button>
            </div>
          </form>
        </div>
      </div>
  
      <!--  -->
      <h2></h2>
      <form @submit.prevent="queryPreferences">
        <div class="form-group">
          <label for="query-font-size">:</label>
          <select v-model="query.font_size" id="query-font-size">
            <option value="">----</option>
            <option value="small"></option>
            <option value="medium"></option>
            <option value="large"></option>
          </select>
        </div>
        <div class="form-group">
          <label for="query-notifications">:</label>
          <select v-model="query.notifications" id="query-notifications">
            <option value="">----</option>
            <option value="true"></option>
            <option value="false"></option>
          </select>
        </div>
        <div class="form-group">
          <label for="query-auto-login">:</label>
          <select v-model="query.auto_login" id="query-auto-login">
            <option value="">----</option>
            <option value="true"></option>
            <option value="false"></option>
          </select>
        </div>
        <div class="form-group">
          <label for="query-verification">:</label>
          <select v-model="query.verification" id="query-verification">
            <option value="">----</option>
            <option value="true"></option>
            <option value="false"></option>
          </select>
        </div>
        <div class="btn-container">
          <button type="submit" class="btn"></button>
        </div>
      </form>
  
      <!--  -->
      <div class="results" v-if="queryResults.length > 0">
        <table>
          <thead>
            <tr>
              <th>ID</th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="result in queryResults" :key="result.id">
              <td>{{ result.id }}</td>
              <td>{{ result.font_size }}</td>
              <td>{{ result.notifications ? '' : '' }}</td>
              <td>{{ result.auto_login ? '' : '' }}</td>
              <td>{{ result.verification ? '' : '' }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div v-else class="results"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PersonalPreference',
  data() {
    return {
      preferences: {
        font_size: '',
        notifications: false,
        auto_login: false,
        verification: false
      },
      newPreference: {
        font_size: 'medium',
        notifications: false,
        auto_login: false,
        verification: false
      },
      query: {
        font_size: '',
        notifications: '',
        auto_login: '',
        verification: ''
      },
      queryResults: [],
      successMessage: false,
      showModal: false,
      isSidebarActive: false
    };
  },
  created() {
    this.loadPreferences();
  },
  methods: {
    loadPreferences() {
      fetch('http://127.0.0.1:5000/get_preferences')
        .then(response => response.json())
        .then(data => {
          this.preferences = data;
        });
    },
    updatePreferences() {
      fetch('http://127.0.0.1:5000/update_preference', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(this.preferences)
      })
      .then(response => {
        if (response.ok) {
          this.showSuccessMessage();
        } else {
          alert('');
        }
      });
    },
    showAddModal() {
      this.showModal = true;
    },
    closeAddModal() {
      this.showModal = false;
    },
    addPreference() {
      fetch('http://127.0.0.1:5000/add_preference', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(this.newPreference)
      })
      .then(response => {
        if (response.ok) {
          this.closeAddModal();
          this.showSuccessMessage();
        } else {
          alert('');
        }
      });
    },
    deletePreference() {
      fetch('http://127.0.0.1:5000/delete_preference', {
        method: 'POST'
      })
      .then(response => {
        if (response.ok) {
          this.showSuccessMessage();
        } else {
          alert('');
        }
      });
    },
    resetPreferences() {
      this.loadPreferences();
    },
    queryPreferences() {
      let query = `http://127.0.0.1:5000/query_preferences?`;
      if (this.query.font_size) query += `font_size=${this.query.font_size}&`;
      if (this.query.notifications) query += `notifications=${this.query.notifications}&`;
      if (this.query.auto_login) query += `auto_login=${this.query.auto_login}&`;
      if (this.query.verification) query += `verification=${this.query.verification}&`;

      fetch(query)
        .then(response => response.json())
        .then(data => {
          this.queryResults = data;
        });
    },
    showSuccessMessage() {
      this.successMessage = true;
      setTimeout(() => {
        this.successMessage = false;
      }, 3000);
    }
  }
};
</script>

<style scoped src="@/assets/css/backend/PersonalPreference.css"></style>
