<template>
  <div class="container">
    <h2></h2>
    <form @submit.prevent="login">
      <input type="hidden" name="next" value="/backend/management" />
      <div class="form-group">
        <label for="email">:</label>
        <input type="email" id="email" v-model="email" required />
      </div>
      <div class="form-group">
        <label for="password">:</label>
        <input type="password" id="password" v-model="password" required />
      </div>
      <div class="form-group">
        <input type="checkbox" id="remember_me" v-model="rememberMe" />
        <label for="remember_me"></label>
      </div>
      <button type="submit" class="btn-login"></button>
    </form>
    <p>
      ? <router-link to="/frontend/register"></router-link>
    </p>
    <p>
      ? <router-link to="/frontend/forgot_password"></router-link>
    </p>
    <p v-if="error">{{ error }}</p>
  </div>
</template>

<script>
import axios from '@/axios'; //  axios 


export default {
  name: 'AppLogin',
  data() {
    return {
      email: '',
      password: '',
      rememberMe: false,
      error: ''
    };
  },
  
  methods: {
        async login() {
      try {
        const csrfToken = getCookie('csrftoken');
        const response = await axios.post('/api/token/', {
          email: this.email,
          password: this.password,
          remember_me: this.rememberMe
        }, {
          headers: {
            'X-CSRFToken': csrfToken
          }
        });
        console.log('', response);
        localStorage.setItem('token', response.data.access); // 
        this.$router.push('/backend/management');
      } catch (error) {
        console.error('', error.response);
        if (error.response && error.response.data) {
          this.error = error.response.data.detail || '';
        } else {
          this.error = '';
        }
      }
    }
  }
};

function getCookie(name) {
  let cookieValue = null;
  if (document.cookie && document.cookie !== '') {
    const cookies = document.cookie.split(';');
    for (let i = 0; i < cookies.length; i++) {
      const cookie = cookies[i].trim();
      if (cookie.substring(0, name.length + 1) === (name + '=')) {
        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
        break;
      }
    }
  }
  return cookieValue;
}
</script>

<style scoped src="@/assets/css/backend/login.css"></style>
